import React, { Component } from "react";
import styles from "./Invoice.module.scss";
import Pdf from "react-to-pdf";
import LineItems from "./LineItems";
import ReactToPrint, { PrintContextConsumer } from "react-to-print";
import uuidv4 from "uuid/v4";
import img_icon from "../../images/imgIcon.svg";
import { Link } from "react-router-dom";
import { Button } from "@material-ui/core";
import table_icon from "../../images/table.svg";
import print from "../../images/printing.svg";
import save from "../../images/save.svg";
import {
  Ajouter_Devis,
  Modifier_Devis,
  Supprimer_Devis,
} from "../../pages/Controller";
const refe = React.createRef();
class Invoice extends Component {
  locale = "en-US";
  currency = "USD";

  componentDidMount(){
    
  let today = new Date();
  let day = today.getFullYear()+'-'+(today.getMonth()+1)+'-'+today.getDate(); 
    this.setState({date:String(day)});
  }
  constructor(props) {
    super(props);
    // this.myRef = React.createRef(),
    this.state = {
      date : "",
      taxRate: 0.0,
      lineItems: [
        {
          id: "initial",
          name: "",
          description: "",
          quantity: 0,
          price: 0.0,
        },
      ],
    };
  }
  componentDidUpdate() {
    console.log(this.state.LineItems);
  }

  handleInvoiceChange = (event) => {
    this.setState({ [event.target.name]: event.target.value });
  };

  handleLineItemChange = (elementIndex) => (event) => {
    let lineItems = this.state.lineItems.map((item, i) => {
      if (elementIndex !== i) return item;
      return { ...item, [event.target.name]: event.target.value };
    });
    this.setState({ lineItems });
  };

  handleAddLineItem = (event) => {
    this.setState({
      // use optimistic uuid for drag drop; in a production app this could be a database id
      lineItems: this.state.lineItems.concat([
        { id: uuidv4(), name: "", description: "", quantity: 0, price: 0.0 },
      ]),
    });
  };

  handleRemoveLineItem = (elementIndex) => (event) => {
    this.setState({
      lineItems: this.state.lineItems.filter((item, i) => {
        return elementIndex !== i;
      }),
    });
  };

  handleReorderLineItems = (newLineItems) => {
    this.setState({
      lineItems: newLineItems,
    });
  };

  handleFocusSelect = (event) => {
    event.target.select();
  };

  handlePayButtonClick = () => {
    alert("Not implemented");
  };

  formatCurrency = (amount) => {
    return new Intl.NumberFormat(this.locale, {
      style: "currency",
      currency: this.currency,
      minimumFractionDigits: 2,
      maximumFractionDigits: 2,
    }).format(amount);
  };

  calcTaxAmount = (c) => {
    return c * (this.state.taxRate / 100);
  };

  calcLineItemsTotal = () => {
    return this.state.lineItems.reduce(
      (prev, cur) => prev + cur.quantity * cur.price,
      0
    );
  };
  saveDevis = () => {
    let taxTotal = this.calcTaxTotal();
    let GrandTotal = this.calcGrandTotal();
    let Devis = {
      date : this.state.date,
      totalHT : taxTotal,
      totalTTC : GrandTotal,
      conditionLivraison : "condition : ",
      conditionReglement : "condition : ",
      conditionGenerale : "generale : "
    }
   Ajouter_Devis(Devis)
    .then(() => alert("Devis ajouter"))
    .catch(e => console.log(e))
  };
  calcTaxTotal = () => {
    return this.calcLineItemsTotal() * (this.state.taxRate / 100);
  };

  calcGrandTotal = () => {
    return this.calcLineItemsTotal() + this.calcTaxTotal();
  };

  render = () => {
    return (
      <div
        ref={(el) => (this.componentRef = el)}
        refe={refe}
        className={styles.invoice}
      >
        <div className="flex flex-row justify-between items-center">
          <div className="brand w-20 p-2 rounded-sm" onClick={() => alert("a")}>
            <img alt="Logo" className="logo" src={img_icon} />
            {this.state.currentDate}
          </div>
          <Button className="list-link-btn">
            <img src={table_icon} />
            <Link to={"/" + this.props.listLink}>
              {"Liste de " + this.props.title}
            </Link>
          </Button>
        </div>
        <div className={styles.addresses}>
          <div className={styles.from}>
            <strong>Socit :</strong> {/*this.props.companyName*/} MOBI SM
            <br />
            <strong>Adresse : </strong>
            {/*this.props.companyAdress*/} Rue ibn charaf
            <br />
            <strong>Code postal : </strong>
            {/*this.props.companyCode*/}&nbsp;{/*this.props.companyVille*/}
            2011 TUNIS
            <br />
            <strong>Numro Tlphone : </strong>
            {/*this.props.companyTel*/} 25 444 789
          </div>
          <div>
            <div className={`${styles.valueTable} ${styles.to}`}>
              <div className={styles.row}>
                <div className={styles.label}>
                  Client : {/*this.props.clientName*/} Abd satar
                </div>
                <div className={styles.value}></div>
              </div>
              <div className={styles.row}>
                <div className={styles.label}>Devis #</div>
                <div className={styles.value}>123456</div>
              </div>
              <div className={styles.row}>
                <div className={styles.label}>Date :</div>
                <div className={`${styles.value} ${styles.date}`}>
                  {this.props.currentDate}
                </div>
              </div>
            </div>
          </div>
        </div>
        <h2>Devis</h2>

        <LineItems
          items={this.state.lineItems}
          currencyFormatter={this.formatCurrency}
          addHandler={this.handleAddLineItem}
          changeHandler={this.handleLineItemChange}
          focusHandler={this.handleFocusSelect}
          deleteHandler={this.handleRemoveLineItem}
          reorderHandler={this.handleReorderLineItems}
        />

        <div className={styles.totalContainer}>
          <form>
            <div className={styles.valueTable}>
              <div className={styles.row}>
                <div className={styles.label}>TVA (%)</div>
                <div className={styles.value}>
                  <input
                    name="taxRate"
                    type="number"
                    step="0.01"
                    value={this.state.taxRate}
                    onChange={this.handleInvoiceChange}
                    onFocus={this.handleFocusSelect}
                  />
                </div>
              </div>
            </div>
          </form>
          <form>
            <div className={styles.valueTable}>
              <div className={styles.row}>
                <div className={styles.label}>Total HT</div>
                <div className={`${styles.value} ${styles.currency}`}>
                  {this.formatCurrency(this.calcLineItemsTotal())}
                </div>
              </div>
              <div className={styles.row}>
                <div className={styles.label}>TVA ({this.state.taxRate}%)</div>
                <div className={`${styles.value} ${styles.currency}`}>
                  {this.formatCurrency(this.calcTaxTotal())}
                </div>
              </div>
              <div className={styles.row}>
                <div className={styles.label}>Total TTC</div>
                <div className={`${styles.value} ${styles.currency}`}>
                  {this.formatCurrency(this.calcGrandTotal())}
                </div>
              </div>
            </div>
          </form>
        </div>

        <div className={styles.pay}>
          <ReactToPrint content={() => this.componentRef}>
            <PrintContextConsumer>
              {({ handlePrint }) => (
                <button className={styles.payNow} onClick={handlePrint}>
                  <span>Imprimer</span>
                  <img src={print} className="w-4" />
                </button>
              )}
            </PrintContextConsumer>
          </ReactToPrint>
          <Pdf targetRef={refe} filename="invoice.pdf">
            {({ toPdf }) => (
              <button className={styles.payNow} onClick={this.saveDevis}>
                <span>Enregistrer</span>
                <img src={save} className="w-4" />
              </button>
            )}
          </Pdf>
        </div>
      </div>
    );
  };
}

export default Invoice;
