import React, { useState } from "react";
import Datatable from "../partials/Datatable";
import Header from "../partials/Header";
import Sidebar from "../partials/Sidebar";
import {
  Afficher_Devis,
  Ajouter_Devis,
  Modifier_Devis,
  Supprimer_Devis,
} from "./Controller";

const Commandes = () => {
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const columns = ["id", "date", "totalHT", "totalTTC","condition","reglment","Condition generale"];

  return (
    <>
      <div className="flex h-screen overflow-hidden">
        {/* Sidebar */}
        <Sidebar sidebarOpen={sidebarOpen} setSidebarOpen={setSidebarOpen} />

        {/* Content area */}
        <div className="relative flex flex-col flex-1 overflow-y-auto overflow-x-hidden white">
          {/*  Site header */}
          <Header sidebarOpen={sidebarOpen} setSidebarOpen={setSidebarOpen} />
          {/*Datatable*/}
          <Datatable
            Ajouter={Ajouter_Devis}
            Afficher={Afficher_Devis}
            Modifier={Modifier_Devis}
            Supprimer={Supprimer_Devis}
            title={"Liste des commandes"}
            subTitle={"Liste des commandes"}
            c={columns}
            x="1"
            eg="1"
          />
        </div>
      </div>
    </>
  );
};

export default Commandes;
